body {
    font-family: 'Minguk-Regular' !important;
    font-weight: 400 !important;
    z-index: auto !important;
    overflow-x: hidden;
    position: relative;
}

body.dimmed::after {
    content:'';
    position: absolute;
    display: block;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0,0,0,.4);
    z-index: 50;
}


